import {BorderBox, Flex, Position} from '@primer/components'
import React from 'react'
import {HEADER_HEIGHT} from './header'
import NavItems from './nav-items'
import { StaticQuery, graphql } from "gatsby";

function Sidebar() {

  return (<StaticQuery
    query={graphql`
    query MenuQuery {
      allNavYaml {
        edges {
          node {
            title
            url
            items {
              title
              url
            }
          }
        }
      }
    }
    `}
    render={({
      allNavYaml: {
        edges
      }
    }) => {
      const navItems = edges.map(item => item.node);
      return (
        <Position
          position="sticky"
          top={HEADER_HEIGHT}
          height={`calc(100vh - ${HEADER_HEIGHT}px)`}
          minWidth={260}
          color="gray.8"
          bg="gray.0"
        >
          <BorderBox
            border={0}
            borderRight={1}
            borderRadius={0}
            height="100%"
            style={{overflow: 'auto'}}
          >
            <Flex flexDirection="column">
              <NavItems items={navItems} />
            </Flex>
          </BorderBox>
        </Position>
      )
    }}/>
  )
}

export default Sidebar
